// src/pages/index.tsx
import Link from "next/link";
import { GetStaticProps } from "next";
import { getAllCases } from "@/lib/getCases"; // returns array with {slug,title,tags,verdict,field,summary,date,published,home}

function Pill({ children }: { children: React.ReactNode }) {
  return (
    <span className="inline-flex items-center rounded-full border px-2 py-0.5 text-xs">
      {children}
    </span>
  );
}

function CaseCard({
  href, title, tags, verdict,
}: { href: string; title: string; tags: string[]; verdict: React.ReactNode }) {
  return (
    <Link href={href} className="block rounded-2xl border p-4 transition hover:shadow-sm">
      <div className="flex items-start justify-between gap-4">
        <h3 className="text-lg font-semibold leading-snug underline decoration-slate-300/70 underline-offset-4">
          {title}
        </h3>
        <div className="shrink-0">{verdict}</div>
      </div>
      <div className="mt-3 flex flex-wrap gap-2 text-slate-600">
        {tags.map((t) => <Pill key={t}>{t}</Pill>)}
      </div>
    </Link>
  );
}

export const getStaticProps: GetStaticProps = async () => {
  const all = getAllCases();
  // include any new case automatically
  const cases = all
    .filter(c => c.published !== false)                   // keep published
    .sort((a,b) => (b.date ?? "").localeCompare(a.date ?? "")) // newest first
    .slice(0, 6);                                         // show up to 6 (or remove)
  return { props: { cases } };
};

export default function Home({ cases }: { cases: any[] }) {
  return (
    <main className="min-h-screen bg-white">
      {/* header â€¦ unchanged â€¦ */}

      {/* cases */}
      <section className="mx-auto mt-2 max-w-5xl px-5 pb-8">
        <div className="grid gap-4 md:grid-cols-2">
          {cases.map(c => (
            <CaseCard
              key={c.slug}
              href={`/cases/${c.slug}/`}               // note trailing slash
              title={c.title}
              verdict={<Pill>
                {c.verdict === "elegant" ? "ðŸ§ª Elegant nonsense"
                  : c.verdict === "nice-demo" ? "ðŸª„ Nice demo"
                  : c.verdict === "over-engineered" ? "ðŸ§° Over-engineered"
                  : "ðŸ§  Genius curiosity"}
              </Pill>}
              tags={[c.field, c.summaryTag ?? c.summaryTagline ?? c.subtitle].filter(Boolean)}
            />
          ))}
        </div>

        <div className="mt-6">
          <Link href="/cases/" className="text-slate-800 underline">
            Browse all cases â†’
          </Link>
        </div>
      </section>

      {/* manifesto + footer â€¦ unchanged â€¦ */}
    </main>
  );
}
